@use "../../sass/variables" as *;

.search-box {
  display: flex;
  flex-direction: column;

  &__label {
    font-size: 20px;
    text-align: center;
    padding-right: 5px;
  }

  &__input {
    margin: 20px 0;
    padding: 10px;
    font-size: 18px;
    border: 5px grey solid;
    border-radius: 15px;
    color: black;
    min-width: 100px;

    &:focus {
      border: 5px $color-primary solid;
      outline: none;
    }
  }
}

@media screen and (min-width: 992px) {
  .search-box {
    &__input {
      width: calc((100% / 3) - 50px);
    }
  }
}
